@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Audiowide', cursive;
  letter-spacing: 3px;
}

:root {
  --white: #fff;
  --hide: none;
  --low: 0;
  --high: 1;
  --animate: show 300ms ease-out;
  --input-bg-color: rgba(255, 255, 255, 0.05);
  --border-color: rgba(0, 0, 0, 0.12);
  --hover-color: #4a4a4a;
  --focus-color: rgba(0, 149, 255, 1);
  --main-transition: all 300ms ease;
}

/* overall menu style */
.wrapper {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgb(0, 0, 0);
  background: linear-gradient(
    129deg,
    rgba(0, 0, 0, 0.9630991046348315) 23%,
    rgba(37, 37, 40, 1) 72%
  );
}

.wrapper ul {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  width: 100%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  list-style: none;
  text-align: center;
}

.wrapper ul li {
  display: inline-block;
  margin: 10px;
  color: #fff;
  color: var(--white);
  text-decoration: none;
  font-size: 30px;
  padding: 5px 10px;
  position: relative;
  transition: all 300ms ease;
  -webkit-transition: all 300ms ease;
  -webkit-transition: var(--main-transition);
  cursor: pointer;
}

.wrapper ul li::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: #fff;
  background: var(--white);
  left: 0;
  top: 0;
  border-radius: 50px;
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
  transition: all 300ms ease;
  -webkit-transition: all 300ms ease;
  -webkit-transition: var(--main-transition);
  transition-property: height, -webkit-transform;
  transition-property: transform, height;
  transition-property: transform, height, -webkit-transform;
  transition-duration: 500ms, 1000ms;
  transition-delay: 0ms, 1000ms;
  z-index: -1;
}

@media (hover: hover) and (pointer: fine) {
  .wrapper ul li:hover::after {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }

  .wrapper ul li:hover {
    color: black;
  }
}

.wrapper ul li.hello {
  pointer-events: none;
  pointer-events: var(--hide);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 3rem;
}

.wrapper ul li.goodbye {
  -webkit-transform: scale(0);
          transform: scale(0);
  /*  margin: 0;
  padding: 0; */
  overflow: hidden;
  display: none;
  /*  height: 0; */
}

/*  list style */

.menu {
  height: 80%;
}

li + div {
  height: 0;
}

.hello + div {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.hide {
  display: none;
  display: var(--hide);
}

.show {
  animation: show 300ms ease-out;
  animation: var(--animate);
  -webkit-animation: show 300ms ease-out;
  -webkit-animation: var(--animate);
  pointer-events: none;
  pointer-events: var(--hide);
}

@keyframes show {
  0% {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
  }
  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
}

@-webkit-keyframes show {
  0% {
    -webkit-transform: scaleY(0);
  }
  100% {
    -webkit-transform: scaleY(1);
  }
}

@media only screen and (max-width: 420px), (max-height: 370px) {
  .wrapper ul li {
    font-size: 1.4rem;
  }
  .wrapper ul li.hello {
    font-size: 1.5rem;
    width: 100%;
  }
  h1.clicked {
    font-size: 2rem;
  }
}
@media only screen and (max-height: 370px) {
}

/* result and  count button */

h1 {
  margin-top: 8vh;
  bottom: 30%;
  font-size: 4rem;
  display: none;
  display: var(--hide);
  color: #fff;
  color: var(--white);
  letter-spacing: 8px;
  font-weight: 500;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  transition: all 300ms ease;
  -webkit-transition: all 300ms ease;
  -webkit-transition: var(--main-transition);
}

.count {
  display: none;
  display: var(--hide);
}

.clicked {
  display: block;
  animation: show 300ms ease-out;
  animation: var(--animate);
  -webkit-animation: show 300ms ease-out;
  -webkit-animation: var(--animate);
}

.hello + div span {
  display: block;
  color: #fff;
  color: var(--white);
  font-weight: 500;
  font-size: 13px;
  -webkit-animation: show 300ms ease-out;
          animation: show 300ms ease-out;
  animation: var(--animate);
  -webkit-animation: (show 300ms ease-out);
  -webkit-animation: (var(--animate));
  margin: 0 10px;
  min-width: 120px;
}

.buttonContainer {
  margin-top: 40px;
  display: -webkit-flex;
  display: flex;
  width: 17em;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

button.clicked {
  font-size: 1em;
  padding: 10px;
  background: transparent;
  color: #fff;
  color: var(--white);
  border: 2px solid rgba(0, 149, 255, 1);
  border: 2px solid var(--focus-color);
  border-radius: 20px/50px;
  cursor: pointer;
  transition: all 300ms ease;
  -webkit-transition: all 300ms ease;
  -webkit-transition: var(--main-transition);
  width: 100px;
}

button.clicked:hover {
  background-color: rgba(0, 149, 255, 1);
  background-color: var(--focus-color);
  color: black;
  opacity: 0.6;
}

ul input {
  border: none;
  outline: none;
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
}

.hello + div input {
  -webkit-transform: scaleY(1);
          transform: scaleY(1);
  border: 1px solid var(--border-color);
  transition: box-shadow 500ms ease,
    border-color 500ms ease, -webkit-transform 500ms ease;
  transition: transform 500ms ease, box-shadow 500ms ease,
    border-color 500ms ease;
  transition: transform 500ms ease, box-shadow 500ms ease,
    border-color 500ms ease, -webkit-transform 500ms ease;
  -webkit-transition: transform 500ms ease, box-shadow 500ms ease,
    border-color 500ms ease;
  background-color: var(--input-bg-color);
  color: var(--white);
  border-radius: 5%;
  height: 40px;
  width: 8rem;
  margin: 10px;
}

ul span {
  display: none;
}

input::-webkit-inner-spin-button {
  display: none;
}
.hello + div input:hover {
  border-color: var(--hover-color);
}

.hello + div input:focus {
  border: 1px solid var(--focus-color);
  box-shadow: 0px 0px 1px 1px var(--focus-color);
}

